@use '../variables/colors' as *;
@use '../variables/size' as *;

@mixin accessibility_onetap_template_front_end() {

	// Roboto
	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 100;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Thin.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 300;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Light.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 400;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Regular.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 500;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Medium.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 700;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Bold.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: normal;
		font-weight: 900;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Black.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 100;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-ThinItalic.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 300;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-LightItalic.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 400;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-Italic.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 500;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-MediumItalic.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 700;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-BoldItalic.ttf) format("truetype");
		font-display: swap;
	}

	@font-face {
		font-family: Roboto;
		font-style: italic;
		font-weight: 900;
		src: local("Roboto"), url(../fonts/Roboto/Roboto-BlackItalic.ttf) format("truetype");
		font-display: swap;
	}

	html.onetap-cursor-feature1 {
		cursor: url(../../assets/images/cursor1.png), default !important;
	}

	html.onetap-cursor-feature2 {
		cursor: url(../../assets/images/cursor2.png), default !important;
	}

	html.onetap-cursor-feature3 {
		cursor: url(../../assets/images/cursor3.png), default !important;
	}

	/* Elementor Builder */
	body.elementor-editor-active .onetap-container-toggle {
		display: none !important;
  	}

  	/* Bricks Builder */
	.bricks-is-editing .onetap-container-toggle {
		display: none !important;
	}

	/* Oxygen Builder */
	.oxygen-builder-body .onetap-container-toggle {
		display: none !important;
	}

	/* WPBakery (Visual Composer) */
	body.vc_editor .onetap-container-toggle {
		display: none !important;
	}

	/* Beaver Builder */
	body.fl-builder-edit .onetap-container-toggle {
		display: none !important;
	}

	/* Divi Builder */
	body.et-fb .onetap-container-toggle {
		display: none !important;
	}

	/* Thrive Architect */
	body.tve_editor_page .onetap-container-toggle {
		display: none !important;
	}

	/* SiteOrigin Page Builder (Backend Preview) */
	body.siteorigin-panels-live-editor .onetap-container-toggle {
		display: none !important;
	}

	/* SeedProd Landing Page Builder */
	body.seedprod-builder-active .onetap-container-toggle {
		display: none !important;
	}	

	// Fix keyboard navigation.
	nav:focus,
	[role="navigation"]:focus,
	h1[tabindex="-1"]:focus,
	h2[tabindex="-1"]:focus,
	h3[tabindex="-1"]:focus,
	h4[tabindex="-1"]:focus,
	h5[tabindex="-1"]:focus,
	h6[tabindex="-1"]:focus,
	[role="heading"]:focus,
	form:not([disabled]):focus,
	button:not([disabled]):focus,
	[role="button"]:not([disabled]):focus,
	img:focus,
	picture:focus,
	svg:focus {
		border: 1px solid currentcolor !important;
	}	

	.onetap-container-toggle {

		button {
			border: 2px solid transparent !important;
			background: none !important;
			padding: 0 !important;
			transition: transform 0.15s ease !important;
			width: auto !important;
			height: auto !important;			

			img {
				box-sizing: border-box !important;
			}			

			&:focus {
				border-radius: 100% !important;
				color: $light-color !important;
				transform: scale(1.05) !important;
			}			
		}		

		// Toggle.
		.onetap-toggle {
			z-index: 9999999999999999 !important;
			position: fixed !important;
			cursor: pointer !important;
			box-shadow: none !important;

			&.design-border1:focus {
				outline: none !important;
			}

			img {
				height: 60px !important;
				width: 60px !important;
				background: $primary-color !important;
				border-radius: 50% !important;
				display: block !important;
			}

			img.design-size1 {
				padding: 10px !important;
				width: 50px !important;
				height: 50px !important;
			}

			img.design-size2 {
				padding: 15px !important;
				width: 65px !important;
				height: 65px !important;
			}

			img.design-size3 {
				padding: 17.5px !important;
				width: 80px !important;
				height: 80px !important;
			}

			img.design-border1 {
				border: solid 2px #fff;
				box-shadow: 0 0 0 4px $primary-color;
			}

			svg {
				width: 55px !important;
				fill: #111 !important;
				border-radius: 100% !important;

				&:hover {
					box-shadow: 0 0 0 5px #0000000d;
					border-radius: 100px;
				}
			}

		}
	}

	nav.onetap-accessibility.onetap-plugin-onetap {
		box-sizing: border-box !important;
		padding: 0 !important;
		margin: 0 !important;
		text-align: left !important;
		line-height: 1.7 !important;
		letter-spacing: 0 !important;
		font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, Cantarell, "Helvetica Neue", Oxygen, "Fira Sans", "Droid Sans", sans-serif !important;
		font-size: 16px !important;
		max-width: $width !important;
		max-height: 100vh !important;
		overflow: auto !important;
		position: fixed !important;
		z-index: 9999999999999999 !important;
		box-shadow: 2px 8px 60px -2px rgba(0, 0, 0, 0.2) !important;
		top: 0 !important;
		-webkit-transition: all 750ms cubic-bezier(0.23, 1, 0.32, 1) !important;
		-moz-transition: all 750ms cubic-bezier(0.23, 1, 0.32, 1) !important;
		-o-transition: all 750ms cubic-bezier(0.23, 1, 0.32, 1) !important;
		transition: all 750ms cubic-bezier(0.23, 1, 0.32, 1) !important;

		* {
			box-sizing: border-box !important;
		}		

		button {
			border: 1px solid transparent !important;
			background: none !important;
			white-space: normal !important;
			width: auto !important;
			height: auto !important;	
			font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, Cantarell, "Helvetica Neue", Oxygen, "Fira Sans", "Droid Sans", sans-serif !important;
			letter-spacing: 0 !important;
			text-transform: none !important;
			font-weight: 400 !important;

			&:focus {
				border: 1px solid currentcolor !important;
				outline-offset: 0 !important;
			}
		}	

		h2,
		h3,
		p,
		span,
		a {
			letter-spacing: 0 !important;
			text-transform: none !important;
			font-family: system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Noto Sans",Ubuntu,Cantarell,"Helvetica Neue",Oxygen,"Fira Sans","Droid Sans",sans-serif !important;
			font-weight: 400 !important;			
		}

		a {
			text-underline-offset: unset !important;
		}

		img {
			vertical-align: middle !important;
			display: inline-block !important;
		}

		svg {
			fill: #111 !important;
		}

		&.onetap-toggle-close {
			right: -$width !important;
		}

		header {
			padding: 0 !important;
			margin: 0 !important;
			top: unset !important;
			text-transform: unset !important;
			overflow: unset !important;
			width: auto !important;
			height: auto !important;
			z-index: unset !important;
		}

		section {
			padding: 0 !important;
			margin: 0 !important;
		}	

		.onetap-container {
			width: 100% !important;

			.onetap-accessibility-settings {
				background: $second-light-color !important;
				height: 100vh !important;

				// Header top.
				header.onetap-header-top {
					position: relative !important;
					display: block !important;
					opacity: 1 !important;

					&::before {
						content: "" !important;
						background: $primary-color !important;
						height: 320px !important;
						width: 100% !important;
						position: absolute !important;
						top: 0 !important;
					}

					// Languages.
					.onetap-languages {
						// fix inherit on theme block {fix}.
						box-sizing: border-box !important;
						align-items: center !important;
						line-height: 20px !important;
						fill: $light-color !important;
						color: $light-color !important;
						background-color: rgba(255, 255, 255, 0.1019607843) !important;
						border-radius: 10rem !important;
						outline: none !important;
						Padding: 8px 14px 8px 8px !important;
						min-width: 110.969px !important;
						left: 29px !important;
						top: 18px !important;
						position: absolute !important;
						display: flex !important;
						flex-wrap: wrap !important;
						gap: 10px !important;
						justify-content: space-between !important;
						cursor: pointer !important;

						&:hover {
							background-color: #ffffff38 !important;
						}

						.onetap-icon {
							display: flex !important;
							flex-wrap: wrap !important;
							align-items: center !important;
							height: 22px !important;
							width: 22px !important;

							svg {
								font-size: 18px !important;
								line-height: 20px !important;
								width: 1em !important;
								height: auto !important;
								fill: $light-color !important;
							}

							img {
								display: none !important;
							}

							img.onetap-active {
								display: block !important;
								width: 100% !important;
								height: auto !important;
							}
						}

						.onetap-text {
							margin: 0 !important;
							padding: 0 !important;

							span {
								margin: 0 !important;
								font-size: 14px !important;
								line-height: 20px !important;
								color: $light-color !important;
							}

							img {
								transition: all 0.35s !important;
								height: auto !important;
								max-width: 100% !important;
								border: none !important;
								border-radius: 0 !important;
								box-shadow: none !important;
								width: 10px !important;
								margin-left: 3px !important;
							}
						}

						&.onetap-active {

							.onetap-text {

								img {
									transform: rotate(180deg) !important;
								}
							}
						}
					}					

					// List of languages.
					.onetap-list-of-languages {
						border-radius: 8px !important;
						background: $second-light-color !important;
						position: absolute !important;
						top: 65px !important;
						left: 29px !important;
						min-width: 171px !important;
						z-index: 9999999999999999 !important;

						ul {
							margin: 0 !important;
							padding: 20px !important;
							list-style: none !important;
							height: 500px !important;
							overflow: auto !important;

							li {
								text-decoration: none !important;
								color: $dark-color !important;
								font-size: 15px !important;
								cursor: pointer !important;
								line-height: 1.6 !important;
								margin-bottom: 10px !important;
								display: flex !important;
								flex-wrap: wrap !important;
								flex-direction: row-reverse !important;
								align-items: center !important;
								justify-content: start !important;
								gap: 10px !important;

								&:last-child {
									margin-bottom: 0 !important;
								}

								button {
									display: flex !important;
									flex-wrap: wrap !important;
									flex-direction: row-reverse !important;
									gap: 10px !important;
									color: $dark-color !important;
									box-shadow: none !important;
									padding: 0 !important;
									line-height: normal !important;
									font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, Cantarell, "Helvetica Neue", Oxygen, "Fira Sans", "Droid Sans", sans-serif !important;
									letter-spacing: 0 !important;
									text-transform: none !important;
									font-weight: 400 !important;									

									img {
										width: 23px !important;
										height: auto !important;
									}
								}
							}
						}
					}

					// Close.
					.onetap-close {
						display: flex;
						flex-wrap: nowrap !important;
						justify-content: center !important;
						align-items: center !important;
						z-index: 9999999999999999 !important;
						padding: 8px !important;
						background: rgba(0, 0, 0, 0.1019607843) !important;
						border-radius: 5em !important;
						top: 20px !important;
						position: fixed !important;
						font-size: 15px !important;
						line-height: 1 !important;
						cursor: pointer !important;

						&:hover {
							background: rgba(0, 0, 0, 0.18) !important;
						}

						svg {
							fill: $light-color !important;
							width: 15px !important;
							height: 15px !important;
						}
					}

					// Info.
					.onetap-site-container {
						position: relative !important;
						top: 85px !important;
						margin-bottom: 85px !important;

						.onetap-site-info {
							min-height: 235px !important;

							.onetap-image {
								width: 47px !important;
								height: auto !important;
								margin: 0 auto !important;
								margin-bottom: 20px !important;

								svg {
									width: 47px !important;
									height: 41px !important;
								}
							}

							.onetap-title {
								margin-bottom: 20px !important;
								text-align: center !important;

								span {
									display: block !important;
									color: $light-color !important;
									padding: 0 !important;
									margin: 0 !important;
									font-size: 28px !important;
									line-height: 32px !important; /* stylelint-disable-line declaration-property-unit-allowed-list */
									font-weight: 600 !important;
									text-rendering: optimizespeed !important;
									word-break: break-word !important;
									text-align: center !important;
								}
							}

							.onetap-information {
								display: flex !important;
								justify-content: center !important;
								align-items: center !important;
								gap: 15px !important;
								opacity: 0.85 !important;
								color: $light-bg !important;								

								.onetap-desc {
									margin: -11px 0 0 0 !important;

									p {
										text-align: center !important;
										color: $light-color !important;
										font-weight: 400 !important;
										font-size: 16px !important;
										line-height: 1.5 !important;
										margin: 0 !important;

										a {
											display: inline-block !important;
											color: $light-color !important;
											text-decoration: underline !important;
											border: 1px solid transparent !important;

											&:focus {
												border: 1px solid $light-color !important;
												outline: none !important;
											}
										}
									}
								}

								.onetap-statement {

									button {
										cursor: pointer !important;
										text-align: center !important;
										color: $light-color !important;
										font-weight: 400 !important;
										font-size: 16px !important;
										line-height: 1.5 !important;
										padding: 2px !important;
										box-shadow: none !important;
										position: relative !important;
										bottom: 4px !important;

										&::before {
											content: "" !important;
											position: absolute !important;
											background: $light-bg !important;
											border-radius: 100% !important;
											width: 5px !important;
											height: 5px !important;
											top: 12px !important;
											left: -10px !important;
										}
									}
								}

								.onetap-hide-toolbar {

									button {
										cursor: pointer !important;
										text-align: center !important;
										color: $light-color !important;
										font-weight: 400 !important;
										font-size: 16px !important;
										line-height: 1.5 !important;
										padding: 2px !important;
										box-shadow: none !important;
										position: relative !important;
										bottom: 4px !important;

										&::before {
											content: "" !important;
											position: absolute !important;
											background: $light-bg !important;
											border-radius: 100% !important;
											width: 5px !important;
											height: 5px !important;
											top: 12px !important;
											left: -10px !important;
										}
									}
								}								
							}
						}
					}
				}

				// Features.
				.onetap-features-container {
					padding: 0 25px 0 25px !important;
					background: $second-light-color !important;

					.onetap-features {
						background: $light-bg !important;
						padding: 25px 25px 25px 25px !important;
						border-radius: 12px 12px 12px 12px !important;
						display: flex !important;
						flex-wrap: wrap !important;
						gap: 18px !important;
						position: relative !important;
						z-index: 999 !important;
						width: 100% !important;

						.onetap-box-feature {
							// fix inherit on theme block {fix}.
							box-sizing: border-box !important;

							position: relative !important;
							width: calc(33.333% - 12px) !important;
							padding: 1.25em 1.25em 1.25em 1.25em !important;
							border-style: solid !important;
							border-width: 1px 1px 1px 1px !important;
							border-color: $border-color !important;
							border-radius: 8px 8px 8px 8px !important;
							min-height: 135px !important;
							display: flex !important;
							flex-direction: column !important;
							justify-content: center !important;
							align-items: center !important;
							transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s !important;
							cursor: pointer !important;

							-webkit-user-select: none !important;

							/* Chrome, Safari, and Opera */
							-moz-user-select: none !important;

							/* Firefox */
							-ms-user-select: none !important;

							/* Internet Explorer/Edge */
							user-select: none !important;

							// Your browser is not supported.
							.onetap-message,
							.onetap-warning {
								display: none !important;
							}

							&.unsupported-message {
								// Message.
								&:hover,
								&.active {
									display: flex !important;

									.onetap-message {
										display: block !important;
										position: absolute !important;
										bottom: 105% !important;
										left: 73% !important;
										background: #000 !important;
										padding: 12px !important;
										border-radius: 8px !important;
										width: 320px !important;

										&::before {
											content: '' !important;
											position: absolute !important;
											top: 100% !important;
											left: 6% !important;
											margin-left: -5px !important;
											border-width: 7px !important;
											border-style: solid !important;
											border-color: #000 transparent transparent transparent !important;
										}

										.title {
											color: $light-color !important;
											font-weight: 600 !important;
											font-size: 16px !important;
											display: block !important;
											text-align: left !important;
											margin-bottom: 8px !important;
										}
										
										.desc {
											color: $light-color !important;
											font-size: 15px !important;
											display: block !important;
											text-align: left !important;
										}

										a.link {
											color: $light-color !important;
											text-decoration: underline !important;
										}
									}
								}

								// Warning.
								.onetap-warning {
									display: block !important;
									position: absolute !important;
									top: 5px !important;
									right: 10px !important;
								}
							}							

							// Icon.
							.onetap-icon {

								.onetap-icon-animation {
									font-size: 30px !important;
									margin-bottom: 15px !important;
									display: block !important;

									svg {
										width: 1em !important;
										height: 1em !important;
										display: block !important;
										fill: #111 !important;
									}
								}
							}

							// Title.
							.onetap-title {

								span {
									display: block !important;
									font-size: 14px !important;
									color: $dark-color !important;
									margin: 0 0 17px 0 !important;
									text-align: center !important;
									line-height: 1.2 !important;
									word-break: break-word !important;
									letter-spacing: 0 !important;
									text-transform: none !important;
									font-family: system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Noto Sans",Ubuntu,Cantarell,"Helvetica Neue",Oxygen,"Fira Sans","Droid Sans",sans-serif !important;
									font-weight: 400 !important;
								}

								p.onetap-option-levels {
									display: flex !important;
									margin: 0 !important;
									justify-content: center !important;

									span.onetap-level {
										height: 4px !important;
										background: $divider-border-color !important;
										width: 28.9px !important;
